
.loading{
   position: fixed;
	outline: none;
	border: none;
	color: #fff;
	text-transform: uppercase;
	letter-spacing: 1px;
	font-size: 12px;
	z-index: 50;
	line-height: 32px;
	overflow: hidden;
	border-radius: 5px;
	left: 50%;
	top: 50%;
	-webkit-transform: translate(-50%,-50%);
	width: 100px;
	text-align: center;
}




